/*page contact*/
.page-contact {
	margin-bottom: 60px;
	.page_title {
		margin-bottom: 30px;
		font-weight: 500;
		font-size: 28px;
	}
	.page-contact_info {
		padding: 20px;
		.headline {
			.title {
				font-weight: 500;
				margin-bottom: 30px;
			}
			.desc {
				line-height: 1.618;
				margin: 0 0 30px;
				color: var(--color-second);
				font-size: var(--body-14);
			}
		}
		.info_body {
			h4 {
				font-weight: 500;
				line-height: 1.3;
				margin-bottom: 30px;
			}
			>div {
				display: flex;
				align-items: center;
				color: var(--color-second);
				margin: 10px 0 ;
				line-height: 25px;
				letter-spacing: 0.2px;
				a {
					color: var(--main-color);
				}
				.svg_element-ui {
					@include margin-end(10px);
					height: 20px;
					color: var(--body-second-color);
				}
				svg {
					width: 20px;
					height: 20px;
					fill: var(--body-second-color);
				}
			}
			.social {
				margin: 60px 0 0;
				svg {
					width: 43px;
					height: 43px;
				}
			}
		}
	}
	.page-contact_form {
		margin-top: 30px;
		padding: 20px;
		height: auto;
		.title {
			margin-bottom: 30px;
			h4 {
				font-weight: 500;
			}
		}
		.form-group {
			margin-bottom: 20px;
			label {
				font-weight: 500;
			}
			textarea {
				height: 150px;
			}
		}
	}
	.map-wrapper {
		margin-top: 30px;
	}
	@media (min-width: 768px) {
		.page-contact_info {
			padding: 30px;
		}
		.page-contact_form {
			padding: 30px;
		}
	}
	@media (min-width: 992px) {
		margin-bottom: 100px;
		.page-contact_form {
			margin-top: 0;
			height: 100%;
		}
	}
}
/*end page contact*/

/*page about us*/
.page-about {
	.page_title {
		padding: 0 0 20px;
		font-weight: 500;
	}
	.section_title-wrapper .section_title-text-1 {
		font-size: var(--heading-size-h3);
	}
	.paragraph-image {
		padding: 60px 0 30px;
	}
	.paragraph-image_content {
		.paragraph_title {
			font-weight: 500;
			margin-bottom: 20px;
		}
		.image {
			margin-bottom: 20px;
			img {
				object-fit: scale-down;
			}
		}
		.paragraph_item {
			margin-bottom: 45px;
			.title {
				margin-bottom: 20px;
				font-weight: 500;
			}
			.content {
				color: var(--color-second);
				line-height: 1.618;
			}
		}
	}
	.team-member {
		padding: 55px 0;
		.member-item {
			padding: 20px 15px;
			background: var(--text-miscellaneous);
			@extend %transition;
			.image {
				margin-bottom: var(--gutter-lg);
				overflow: hidden;
				img {
					@extend %transition;
					@extend %radius-circle;
				}
			}
			.name {
				font-weight: 500;
				line-height: 1.4;
				margin-bottom: 5px;
			}
			.position {
				color: var(--body-second-color);
				font-size: var(--body-14);
			}
			.opinion {
				margin-top: 20px;
				color: var(--color-second);
			}
			.social {
				text-align: center;
				margin-top: 25px;
				svg {
					width: 26px;
					height: 26px;
					margin: 0 3px;
					path {
						fill: var(--body-second-color);
					}
				}
			}
			&:hover {
				@media (hover:hover) {					
					box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.05);
					.image {
						img {
							transform: scale(0.95);
						}
					}
				}
			}
		}
	}
	.testimonial {
		padding: 60px 0;
		.tns-wrapper {
			max-width: 850px;
			margin: 0 auto;
			margin-top: 70px;
			.tns-nav {
				margin-top: 40px;
			}
		}
		.testimonial-item {
			display: inline-flex;
			flex-wrap: wrap;
		}
		.rating {
			margin-bottom: 20px;
		}
		.image {
			width: 100%;
			max-width: 110px;
			height: 110px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 30px;
			img {
				@extend %radius-circle;
			}
		}
		.info {
			text-align: center;
			.content {
				@include text-left;
				font-size: var(--body-14);
				font-weight: 300;
				line-height: 1.618;
				margin: 0 0 20px;
			}
			.name {
				font-weight: 500;
				letter-spacing: 0.3px;
			}
		}
		.tns-nav {
			margin-top: 50px;
		}
	}
	.statistics {
		padding: 60px 0 40px;
		.statistics_item {
			background: var(--miscellaneous-color);
			padding: 40px 20px;
			justify-content: center;
			@extend %transition;
			.icon {
				@include margin-end(20px);
			}
			svg {
				width: 40px;
				height: 40px;
				path {
					fill: var(--main-color);
				}
			}
			.text_1 {
				font-size: 32px;
			}
			.text_2 {
				font-size: var(--body-14);
				color: var(--body-second-color);
			}
			&:hover {
				@media (hover:hover) {
					box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.09);
				}
			}
		}
	}
	.logo-list {
		padding: 10px 0 50px;
		.logo-list_content {
			position: relative;
		}
		.js-slider-controls {
			a {
				top: 50%;
				margin-top: -18px;
			}
			.js-prev-button {
				right: auto;
				left: 0;
			}
		}
		.tns-nav {
			margin-top: 50px;
		}
	}
	@media (min-width: 768px) {
		.page_title {
			padding-bottom: 35px;
		}
		.paragraph-image {
			padding-bottom: 60px;
		}
		.team-member .member-item {
			padding: 30px;
		}
		.testimonial {
			.testimonial-item {
				flex-wrap: nowrap;
			}
			.image {
				@include margin-end(30px);
			}
			.info {
				@include text-left;
				.content {
					font-size: 18px;
				}
			}
		}
		.logo-list {
			padding: 40px 0 80px;
		}
	}
}
/*end page about us*/

/* other page */
.page-content {
	margin: 30px 0 50px;
}
/* end other page */